.twicon-ul {
  list-style-type: none;
  padding-left: 0;
  margin-left: 0;
}
.twicon-ul > li {
  position: relative;
  line-height: 2em;
}
.twicon-ul > li .twicon {
  display: inline-block;
  vertical-align: middle;
}
.twicon-border {
  border: solid 0.08em #f1f1f1;
  border-radius: 0.1em;
  padding: 0.2em 0.25em 0.15em;
}
.twicon-pull-left {
  float: left;
}
.twicon-pull-right {
  float: right;
}
.twicon.twicon-pull-left {
  margin-right: 0.3em;
}
.twicon.twicon-pull-right {
  margin-left: 0.3em;
}
.twicon-spin {
  -webkit-animation: twicon-spin 2s infinite linear;
  animation: twicon-spin 2s infinite linear;
  display: inline-block;
}
.twicon-pulse {
  -webkit-animation: twicon-spin 1s infinite steps(8);
  animation: twicon-spin 1s infinite steps(8);
  display: inline-block;
}
@-webkit-keyframes twicon-spin {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes twicon-spin {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.twicon-rotate-90 {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.twicon-rotate-180 {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}
.twicon-rotate-270 {
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}
.twicon-flip-horizontal {
  -webkit-transform: scale(-1, 1);
  transform: scale(-1, 1);
}
.twicon-flip-vertical {
  -webkit-transform: scale(1, -1);
  transform: scale(1, -1);
}
.twicon-flip-horizontal.twicon-flip-vertical {
  -webkit-transform: scale(-1, -1);
  transform: scale(-1, -1);
}
:root .twicon-flip-horizontal,
:root .twicon-flip-vertical,
:root .twicon-rotate-180,
:root .twicon-rotate-270,
:root .twicon-rotate-90 {
  -webkit-filter: none;
  filter: none;
  display: inline-block;
}
.twicon-inverse {
  color: #fff;
}
.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}
