/* GLOBAL STYLES */

.main-nav {
  max-width: 1200px;
}

.navigation ul {
  background: #fff;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navigation li {
  flex: 3;
}

.navigation .user {
  flex: 1;
}


/* HEADER */

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.header {
  overflow: hidden;
  padding: 16px 10px;
}

.header a {
  float: left;
  text-align: center;
  padding: 14px;
  text-decoration: none;
  font-size: 18px;
  line-height: 23px;
  border-radius: 4px;
  margin-right: 10px;
}

.header a.logo {
  font-size: 22px;
}

.header a:hover {
  background-color: #ddd;
}

.header a.active {
  background-color: rgb(110, 172, 125);
  color: white;
}

.header-right {
  float: right;
}

@media screen and (max-width: 550px) {
  .header a {
    padding: 8px;
    font-size: 12px;
    line-height: 15px;
    margin-right: 2px;
  }
  .header a.logo {
    font-size: 18px;
    font-weight: bold;
  }
}
